<template>
  <div>
    <main> <!-- Début du main -->
      <div class="title"> <!-- title de présentation, dans le main -->
        <h1>
          <BarLoader style="margin: 0 auto; border-radius:5px" v-if="personLoading"></BarLoader>
          <StateDisplay :state="getPerson.state"/>
          {{getPerson.name}}
          <span v-if="getPerson.date" class="date">({{getPerson.date}})</span>
        </h1>
        <span class="actions" v-if="jwt">
            <img src="@/assets/icon/edit.svg" class="action" @click="$router.push('/'+getPerson.slug+'/edit')">
        </span>
      </div>
      <!-- <input v-if="!personLoading" class="print" type="button" value="Imprimer cette fiche" @click="print()" /> -->
      <input type="button" class="print" value="Imprimer cette fiche" @click="$router.push(getPerson.slug+'/print')">
      <div class="card content" v-if="personLoading">
        <BarLoader style="margin: 0 auto; border-radius:5px"></BarLoader>
      </div>
      <div class="card content" v-else>
        <viewer :value="getPerson.bio"/>
      </div>
      
      <ul class="card artworks" v-if="getArtworks.length != 0 || jwt">
        <h2>Œuvres</h2>
        <BarLoader style="margin: 0 auto; border-radius:5px" v-if="artworksLoading"></BarLoader>
        <li v-for="artwork in getArtworks" :key="artwork.id">
          <router-link :to="getPerson.slug+'/'+artwork.slug">
            <span class="title" itemprop="name">
              {{artwork.name}}
            </span>
            <span class="date" v-show="artwork.date" ref="date" itemprop="date">({{artwork.date}})</span> <StateDisplay :state="artwork.state"/>
          </router-link>
          <span class="actions" v-if="jwt">
              <img src="@/assets/icon/delete.svg" @click="deleteArtwork(artwork)" class="action">
          </span>
        </li>
        <NewCard type="artwork"></NewCard>
      </ul>
      <div class="card notes" v-if="getPerson.notes">
        <h2>Notes</h2>
        <BarLoader style="margin: 0 auto; border-radius:5px" v-if="personLoading"></BarLoader>
        <viewer :value="getPerson.notes" />
      </div>
    </main><!-- fin main -->
  </div>
</template>
<script>
// @ is an alias to /src
import NewCard from '@/components/card/new.vue'
import StateDisplay from '@/components/state/Display.vue'

import { mapActions, mapGetters } from 'vuex'
// import 'tui-editor/dist/tui-editor-contents.css';
// import 'highlight.js/styles/github.css';
import Viewer from '@toast-ui/vue-editor/src/Viewer.vue'

export default {
  name: 'home',
  components: {
    NewCard,
    Viewer,
    StateDisplay
  },
  // Fetches posts when the component is created.
  created () {
    window.scrollTo(0, 0)
    this.clearArtwork()
    this.fetchPerson(this.$route.params.person)   
    .then(()=>{
      this.fetchArtworks(this.getPerson.slug)
    })
  },
  computed: {
        ...mapGetters('jwt',[
        'jwt',
        'jwtSubject',
        'jwtIssuer'
        ]),
        ...mapGetters('persons',[
        'getPerson',
        'personLoading',
        'getArtworks',
        'artworksLoading'
        ]),
    },
    methods: {
        ...mapActions('persons', [
      'clearPerson',
      'fetchPerson',
      'fetchArtworks',
      'deletePerson',
      'deleteArtwork'
        ]),
        ...mapActions('artworks', [
      'clearArtwork',
        ]),
      print() {
        window.print()
      }
    }
}
</script>
